:root {
  --speed: 300ms;
  --time-function: ease-in-out;
  --vh: 1vh;
  --desktop: 1200;
}

/* FONT SourceSansPro */
/*images*/
/*=======================START-MEDIA=====================*/
/*=======================END-MEDIA=====================*/
/* media breakpoint mixin */
.blog-section__img {
  border-radius: 10px;
}

.form {
  display: flex;
  flex-direction: column;
  max-width: 626px;
}

.form__field {
  border-bottom: 1px solid #000;
  position: relative;
}

.form__label {
  position: absolute;
  position: absolute;
  transform: translateY(-50%);
  top: 50%;
  left: 32px;
  transition-property: all;
  transition-duration: 300ms;
  transition-timing-function: ease-in-out;
  text-transform: uppercase;
  color: #848484;
}

.form__input {
  padding: 18px 32px;
  border: none;
  width: 100%;
}

.form__input:focus + label {
  transform: translateY(calc(-100% - 10px));
}

.form__btn {
  display: flex;
  justify-content: center;
  margin-top: 30px;
}

.form__btn .btn {
  text-transform: uppercase;
}

.default-hero {
  padding-top: 10px;
}

.default-hero__small {
  padding-bottom: 30px;
}

.default-hero__img {
  margin-top: 10px;
}

@media only screen and (max-width: 1200px) {
  .default-hero__img {
    width: 100%;
    height: auto;
  }
}

.default-hero__heading {
  margin-top: 50px;
  max-width: 609px;
}

@media only screen and (min-width: 768px) {
  .blog-section__row {
    display: flex;
    flex-wrap: wrap;
    gap: 50px 18px;
  }
}

@media only screen and (max-width: 768px) {
  .blog-section__item:not(:last-child) {
    margin-bottom: 35px;
  }
}

@media only screen and (min-width: 768px) {
  .blog-section__item {
    flex-basis: calc((100% - 1 * 18px) / 2);
  }
}

@media only screen and (min-width: 1200px) {
  .blog-section__item {
    flex-basis: calc((100% - 3 * 18px) / 4);
  }
}

.blog-section__description {
  margin-top: 30px;
  max-width: 268px;
}

.blog-section__btn-wrapper {
  display: flex;
  justify-content: center;
  margin-top: 30px;
}

@media only screen and (max-width: 1200px) {
  .blog-section__btn-wrapper {
    display: none;
  }
}

.blog-section__img {
  overflow: hidden;
}

@media only screen and (max-width: 1200px) {
  .blog-section__img,
  .blog-section__img img {
    width: 100%;
    height: auto;
  }
}

.blog-section__img img {
  transition-timing-function: ease-in-out;
  transition-duration: 300ms;
  transition-property: all;
}

@media (any-hover: hover) {
  .blog-section__img img:hover {
    transform: scale(1.2);
  }
}

.blog-section__tags {
  margin-bottom: 50px;
}

.blog-section__btn-wrapper-large {
  margin-top: 50px;
}

.contact:after {
  width: 852px;
  height: 852px;
  background-image: url("../img/decor-large.png");
  transform: translateX(30%);
  right: 0px;
  top: 221px;
}

@media only screen and (max-width: 1200px) {
  .contact:after {
    display: none;
  }
}

@media only screen and (min-width: 1000px) {
  .contact__top, .contact__bottom {
    display: flex;
    justify-content: space-between;
    gap: 20px;
  }
}

@media only screen and (min-width: 1000px) {
  .contact__top {
    align-items: center;
    padding: 0 25px;
  }
}

.contact__title {
  text-transform: uppercase;
}

.contact__img {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 43px;
}

@media only screen and (min-width: 1000px) {
  .contact__img {
    max-width: 468px;
    margin-bottom: 0px;
  }
}

@media only screen and (max-width: 1000px) {
  .contact__img img {
    width: 139px;
    height: auto;
  }
}

@media only screen and (min-width: 576px) and (max-width: 1000px) {
  .contact__form {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

@media only screen and (min-width: 576px) and (max-width: 1000px) {
  .contact__title {
    text-align: center;
  }
}

.contact__bottom {
  margin-top: 50px;
}

.contact__map {
  width: 100%;
  margin-top: 40px;
}

@media only screen and (min-width: 1000px) {
  .contact__map {
    max-width: 867px;
    margin-top: 0px;
  }
}

.contact__map iframe {
  width: 100%;
}

@media only screen and (max-width: 1200px) {
  .contact__map iframe {
    height: 310px;
  }
}

.contact__address {
  max-width: 386px;
  width: 100%;
}

.contact__address h3 {
  text-transform: uppercase;
}

.contact__social {
  margin-top: 40px;
}
